.contact-icons{display:flex;justify-content:space-evenly;font-size:2.5rem}h1{font-family:"Amatic SC",cursive}h2{margin:2rem}h3{margin:0 2rem}p{margin:2rem}#instagram-page #instafeed img{width:49vw;margin:0 .1rem}body{margin:0;font-family:"Open Sans",sans-serif;background-color:#f4eaac}body header{background-color:#c53200;color:#381a06;height:85px;display:flex;align-items:center;justify-content:center;position:fixed;width:100vw}body header img{width:65px}body nav{background-color:#2da4a8;display:grid;grid-template-columns:repeat(4, 1fr);justify-content:center;align-items:center;position:fixed;bottom:0;left:0;right:0;height:70px;text-align:center}body nav .nav-button{opacity:0.8;transform:scale(0.9);transition:opacity 120ms, transform 120ms ease-out}body nav .nav-button.active{opacity:1;transform:scale(1)}body nav .nav-button .icon i{font-size:10vw}body nav .nav-button .icon-title{display:none}body .main-content{height:calc(100vh - 155px);display:grid;grid-template-columns:repeat(4, 100vw);overflow-x:scroll;scroll-snap-type:x mandatory;top:85px;position:relative}body .main-content .page{scroll-snap-align:start}
